<template>
  <div class="w-100 orderTable" style="padding-left: 30px;">
    <div class="p-tb-30">
      <!-- <h2 class="left f-18" style="width: 150px;color: #0D906E;padding-top: 3px;font-weight: normal;">| </h2> -->
      <ul class="left orderUl" style="width: 400px;padding-top: 5px;">
        <li :class="orderNavActive == 0?'active':''" @click="orderTab(0)"></li>
        <li :class="orderNavActive == 1?'active':''" @click="orderTab(1)"></li>
        <li :class="orderNavActive == 2?'active':''" @click="orderTab(2)"></li>
      </ul>
      <div class="right m-r-20"></div>
      <div class="clear"></div>
    </div>
    <div class="p-20" style="background:rgba(229,246,247,1);">
      <el-row :gutter="10">
        <el-form :inline="true" class="demo-form-inline">
          <el-col>
            <el-form-item label="" class="m-b-0">
              <el-input v-model="search.name" placeholder="" style="width: 160px;"></el-input>
            </el-form-item>
            <el-button type="success" icon="el-icon-search" @click="init"></el-button>
          </el-col>
        </el-form>
      </el-row>
    </div>
    <el-table :data="tableData" :max-height="maxHeight" style="width: 100%" id="printBox">
      <el-table-column prop="date" label="" width="80">
        <template slot-scope="scope">{{scope.row.id}}</template>
      </el-table-column>
      <el-table-column prop="name" label="">
        <template slot-scope="scope">{{scope.row.name}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.city}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.address}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.legalPerson}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.legalPersonPhone}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-image
            style="width: 100px; height: 100px"
            :src="scope.row.businessLicensePic"
            fit="fill"
            @click="viewPhoto(scope.row.businessLicensePic)"
          ></el-image>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-image
            style="width: 100px; height: 100px"
            :src="scope.row.doorPic"
            fit="fill"
            @click="viewPhoto(scope.row.doorPic)"
          ></el-image>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.salesman}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.createdDate}}</template>
      </el-table-column>
      <el-table-column label="" width="180" align="center" v-if="orderNavActive == 0">
        <template slot-scope="scope">
          <el-button
            type="success"
            plain
            class="xiugai"
            size="mini"
            @click="xiugai(scope.row)"
          ></el-button>
          <el-button type="success" size="mini" @click="agreeApply(scope.row)"></el-button>
          <el-button type="danger" size="mini" @click="refuseApply(scope.row)"></el-button>
        </template>
      </el-table-column>
      <el-table-column label="" width="180" align="center" v-if="orderNavActive == 1">
        <template slot-scope="scope">
          <el-upload
            class="avatar-uploader"
            action="/api/vehicle/file/uploadPicture"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            @click.native="getRow(scope.row)"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="scope.row.qrCodePic" :src="scope.row.qrCodePic" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <p v-if="scope.row.qrcode" style="text-align:center;">
            <font color="red"></font>
          </p>
        </template>
      </el-table-column>
      <el-table-column label="" width="180" align="center" v-if="orderNavActive == 1">
        <template slot-scope="scope">
          <el-button
            type="success"
            plain
            class="xiugai"
            size="mini"
            @click="xiugai(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            size="mini"
            class="xiugai"
            @click="sesetPassword(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
      <el-table-column label="" width="180" align="center" v-if="orderNavActive == 2">
        <template slot-scope="scope">{{scope.row.rejectReason}}</template>
      </el-table-column>
    </el-table>
    <div class="p-tb-10 text-center">
      <el-pagination
        :hide-on-single-page="pageLength === 0"
        background
        layout="prev, pager, next"
        :page-size="size"
        @current-change="nextPage"
        :total="pageLength"
      ></el-pagination>
    </div>
    <el-dialog title="" center :visible.sync="dialogVisible" :modal="false" width="50%">
      <div style="text-align:center;">
        <el-image :src="src"></el-image>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="success" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible2"
      :modal-append-to-body="false"
      width="30%"
      :modal="false"
      center
      class="xiu"
    >
      <el-form :model="editPartsForm" ref="addFormRef" label-width="80px">
        <el-form-item label="" prop="receiveSide">
          <el-input v-model="editPartsForm.name" placeholder="" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="amount">
          <el-input v-model="editPartsForm.city" placeholder="" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="amount">
          <el-input v-model="editPartsForm.address" placeholder="" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="amount">
          <el-input v-model="editPartsForm.legalPerson" placeholder="" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="amount">
          <el-input v-model="editPartsForm.legalPersonPhone" placeholder="" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="amount">
          <el-input v-model="editPartsForm.positionWd" placeholder="" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="amount">
          <el-input v-model="editPartsForm.positionJd" placeholder="" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item label label-width="120px">
          <div class="yinye"></div>
          <el-upload
            ref="uploadImg2"
            action="/api/vehicle/file/uploadPicture"
            list-type="picture-card"
            :limit="1"
            :file-list="editPartsForm.businessLicensePic"
            :on-remove="handleRemove"
            :on-success="handleSuccess"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label label-width="120px">
          <div class="yinye"></div>
          <el-upload
            ref="uploadImg"
            action="/api/vehicle/file/uploadPicture"
            list-type="picture-card"
            :limit="1"
            :on-remove="handleRemove2"
            :on-success="handleSuccess2"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="reason">
          <el-select v-model="editPartsForm.salesman" placeholder="" style="width: 300px">
            <el-option
              v-for="item in list"
              :key="item.value"
              :label="item.realName"
              :value="item.realName"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="danger" @click="dialogVisibleQ"> </el-button>
        <el-button type="success" @click="dialogVisibleT"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  getBusiness,
  auditGarage,
  resetPassword,
  updateQrCode,
  salesmanList,
  changeGarageInfo
} from "../../request/api.js";
import moment from "moment";
export default {
  name: "order",
  data() {
    return {
      tableData: [],
      list: [],
      editPartsForm: [],
      orderNavActive: 0,
      dialogVisible2: false,
      maxHeight: undefined,
      search: {
        name: ""
      },
      pageLength: null,
      size: 10,
      page: 0,
      dialogVisible: false,
      src: "",
      editPartsForm: {}, //
      uploadInfo: {}
    };
  },
  created() {
    this.init();
  },
  mounted() {
    this.maxHeight = $(".tabBox").height() - 230;
  },
  methods: {
    //
    handleSuccess(e) {
      // console.log(e.data);
      this.editPartsForm.businessLicensePic = e.data;
    },
    //
    handleSuccess2(e) {
      // console.log(e.data);
      this.editPartsForm.doorPic = e.data;
    },
    //
    handleRemove(file, fileList) {
      // console.log(file);
      this.editPartsForm.businessLicensePic = "";
      const filepath = file.response.data;
      // console.log(file.response.data)
    },
    //
    handleRemove2(file, fileList) {
      // console.log(file);
      this.editPartsForm.doorPic = "";
      const filepath = file.response.data;
      // console.log(file.response.data)
    },
    //
    xiugai(row) {
      this.editPartsForm = row;
      console.log(row);
      salesmanList().then(res => {
        console.log(res.data.data);
        this.list = res.data.data;
      });
      this.dialogVisible2 = true;
    },
    dialogVisibleQ() {
      this.$refs.addFormRef.resetFields();
      this.$refs.uploadImg.clearFiles();
      this.$refs.uploadImg2.clearFiles();
      this.editPartsForm.doorPic = "";
      this.editPartsForm.businessLicensePic = "";

      this.dialogVisible2 = false;
    },
    //
    dialogVisibleT() {
      console.log(this.editPartsForm);
      if (!this.editPartsForm.name)
        return this.$message.error("");
      if (!this.editPartsForm.city) return this.$message.error("");
      if (!this.editPartsForm.address)
        return this.$message.error("");
      if (!this.editPartsForm.legalPerson)
        return this.$message.error("");
      if (!this.editPartsForm.legalPersonPhone)
        return this.$message.error("");
      if (!this.editPartsForm.positionJd)
        return this.$message.error("");
      if (!this.editPartsForm.positionWd)
        return this.$message.error("");
      // if (this.editPartsForm.businessLicensePic == "")
      //   return this.$message.error("");
      // if (this.editPartsForm.doorPic == "")
      //   return this.$message.error("");
      if (!this.editPartsForm.salesman)
        return this.$message.error("");
      changeGarageInfo(this.editPartsForm).then(res => {
        if (res.data.code == 200) {
          this.$message({
            type: "success",
            message: "!"
          });

          this.$refs.addFormRef.resetFields();
          this.$refs.uploadImg.clearFiles();
          this.$refs.uploadImg2.clearFiles();
          this.editPartsForm.doorPic = "";
          this.editPartsForm.businessLicensePic = "";
          this.init();
          this.dialogVisible2 = false;
        }
      });
    },
    init() {
      const data = {
        size: this.size,
        page: this.page,
        status: this.orderNavActive,
        name: this.search.name
      };
      getBusiness(data).then(res => {
        console.log(res.data);
        const result = res.data;
        this.pageLength = result.pageInfo.total+10;
        this.tableData = result.data;
      });
    },
    nextPage(page) {
      this.page = page - 1;
      this.init();
    },
    orderTab(index) {
      this.orderNavActive = index;
      this.page = 0;
      this.init();
    },
    viewPhoto(url) {
      this.src = url;
      this.dialogVisible = true;
    },
    getRow(row) {
      this.uploadInfo = row;
    },
    handleAvatarSuccess(res, file) {
      const data = {
        id: this.uploadInfo.id,
        qrCodePic: res.data
      };
      updateQrCode(data).then(res => {
        if (res.data.code == 200) {
          this.$message({
            type: "success",
            message: ""
          });
          this.init();
        }
      });
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isLt2M;
    },
    agreeApply(row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          let data = JSON.parse(JSON.stringify(row));
          data.status = 1;
          auditGarage(data).then(res => {
            const result = res.data;
            if (result.code == 200) {
              this.$message({
                type: "success",
                message: ""
              });
              this.init();
            } else {
              this.$message({
                type: "error",
                message: result.message
              });
            }
          });
        })
        .catch(() => {});
    },
    refuseApply(row) {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(({ value }) => {
          let data = JSON.parse(JSON.stringify(row));
          data.status = 2;
          data.rejectReason = value;
          auditGarage(data).then(res => {
            const result = res.data;
            if (result.code == 200) {
              this.$message({
                type: "success",
                message: ""
              });
              this.init();
            } else {
              this.$message({
                type: "error",
                message: result.message
              });
            }
          });
        })
        .catch(() => {});
    },
    sesetPassword(row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          resetPassword({ gid: row.id }).then(res => {
            const result = res.data;
            if (result.code == 200) {
              this.$message({
                type: "success",
                message: ""
              });
              this.init();
            } else {
              this.$message({
                type: "error",
                message: result.message
              });
            }
          });
        })
        .catch(() => {});
    }
  }
};
</script>
<style scoped>
.orderUl li {
  float: left;
  padding: 4px 18px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
  transition: 0.5s;
  font-size: 16px;
}
.yinye {
  float: left;
  width: 80px;
  padding: 0 12px 0 0;
  text-align: right;
  vertical-align: middle;
}
.orderUl li:hover {
  background: rgba(0, 0, 0, 0.1);
  transition: 0.5s;
}

.orderUl li.active {
  background: #3ac29f;
  color: #fff;
}
.xiugai {
  margin-bottom: 8px;
  width: 125px;
  margin-left: 8px;
}
</style>
<style>
.xiu .el-form-item__content {
  margin-left: 0 !important;
}
.xiu .el-upload-list__item-actions {
  width: 80px;
  height: 80px;
}
.xiu .el-upload-list--picture-card .el-upload-list__item-thumbnail {
  width: 80px;
  height: 80px;
}
.xiu .el-upload-list__item {
  width: 80px;
  height: 80px;
}
.xiu .el-dialog--center .el-dialog__body {
  padding: 5px 25px 30px;
}
.xiu .el-input__inner {
  height: 35px;
}
.xiu .el-form-item {
  margin-bottom: 10px;
}
.xiu .el-upload--picture-card {
  width: 80px;
  height: 80px;
  line-height: 80px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
}
.avatar {
  width: 100px;
  height: 100px;
  display: block;
}
</style>




