var websock = null;
let rec; //5WebSocket  rec
let isConnect = false; // 
let checkMsg = "heartbeat"; ///   

var globalCallback = function () {};

let createWebSocket = () => {
    try {
        var ws = "ws://47.96.248.209:5280";
        websock = new WebSocket(ws);
        initWebSocket(); //websocket
    } catch (e) {
        console.log("");
        reConnect(); //webSocket 
    }
};

//
let reConnect = () => {
    console.log("");
    if (isConnect) return; //
    rec && clearTimeout(rec);
    rec = setTimeout(function () { // 5  
        createWebSocket();
    }, 5000);
};
//
let closeWebSocket = () => {
  webSocket.close();
};
//
var heartCheck = {
    timeout: 20000, // 20s
    timeoutObj: null, //

    start: function () {
        this.timeoutObj = setTimeout(function () {
            if (isConnect) websock.send(checkMsg);
        }, this.timeout);
    },

    reset: function () {
        clearTimeout(this.timeoutObj);
        this.start();
    }
};

// websocket
function initWebSocket() {
    // ws -->
    let uName = localStorage.getItem("uName")
    var ws =  "ws://47.96.248.209:5280";
    websock = new WebSocket(ws)
    websock.onmessage = function (e) {
        websocketonmessage(e)
    }
    websock.onclose = function (e) {
        websocketclose(e)
    }
    websock.onopen = function () {
        websocketOpen()
        // heartCheck.start();
    }

    // 
    websock.onerror = function () {
        console.log('WebSocket')
        isConnect = false; //
        reConnect(); // 
    }
}



// 
function sendSock(agentData, callback) {
    globalCallback = callback
    // console.log(globalCallback)
    if (websock.readyState === websock.OPEN) {
        // ws
        websocketsend(agentData)
    } else if (websock.readyState === websock.CONNECTING) {
        //  1s
        setTimeout(function () {
            sendSock(agentData, callback)
        }, 1000)
    } else {
        //  1s
        setTimeout(function () {
            sendSock(agentData, callback)
        }, 1000)
    }
}

function getSock(callback) {
    globalCallback = callback
}
// 
function websocketonmessage(e) {
    console.log(e.data)



    let O_o = JSON.parse(decodeUnicode(e.data))

    if (!O_o) {
        heartCheck.reset();
    } else {
        if (O_o.msg == "open success") {
            sessionStorage.setItem("wid", O_o.wid);
        } else {
            console.log(O_o);
            globalCallback(O_o);
        }
    }


    // globalCallback(JSON.parse(e.data))
    function decodeUnicode(str) {
        str = str.replace(/\\/g, "%");
        //
        str = unescape(str);
        //
        str = str.replace(/%/g, "\\");
        //
        str = str.replace(/\\/g, "");
        return str;
    }
}

// 
function websocketsend(agentData) {
    console.log(JSON.stringify(agentData))
    websock.send(JSON.stringify(agentData))
}

// 
function websocketclose(e) {
    console.log(e)
    isConnect = false ; //
    console.log('connection closed (' + e.code + ')')
}

//  websocket 
function websocketOpen(e) {
    console.log('')
}

initWebSocket()

// 
export {
    sendSock,
    getSock,
    createWebSocket,
    closeWebSocket
}


